@import '../colors';

.scene{
    position: fixed;
    width: 100%;
    transition: all 1s ease-in-out;
    .info-btn{
        position: absolute;
        z-index: 1;
        font-size: 3rem;
        top: 5vh;
        left: 5vw;
        color: rgba(0,0,0,0.3);
    }
    .info-btn:hover{
        cursor: pointer;
    }
}