.projects-menu{
    position: fixed;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    .projects-menu-wrapper{
        position: relative;
        height: 70vh;
        width: 70vw;
        background-color: lightblue;
        border-radius: 5px;
        .close{
            position: absolute;
            top:0;
            right: 0;
        }
        .close:hover{
            cursor: pointer;
        }
    }
}