@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{
  margin: 0;
  padding: 0;
  overflow-y: hidden;
}

h1, h2 h3, h4, h5, h6, p, a{
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none;
  color: black;
}

.App {

}

.page-enter {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
  
.page-enter-active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    transition: all 500ms ease-in-out;
}
  
.page-exit {
    -webkit-transform: translateX(0) translateY(-100%);
            transform: translateX(0) translateY(-100%);
}
  
.page-exit-active {
    -webkit-transform: translateX(-100%) translateY(-100%);
            transform: translateX(-100%) translateY(-100%);
    transition: all 500ms ease-in-out;
}
nav{position:fixed;z-index:5;background-color:#00b8cd;box-shadow:0 0 2px 2px rgba(0,0,0,.1);right:0;top:0;height:10vh;width:10vh;display:grid;place-items:center}nav .burger-wrapper{position:fixed;right:2.5vh;top:2.5vh;height:10vh;width:10vh;display:grid;place-items:center}nav .burger-container{z-index:1;overflow:visible;position:relative;cursor:pointer}nav .burger-1,nav .burger-2,nav .burger-3{margin-bottom:6px;width:30px;height:6px;border-radius:6px;background-color:#000;opacity:1;cursor:pointer;transition:opacity 300ms linear,-webkit-transform 300ms linear;transition:transform 300ms linear,opacity 300ms linear;transition:transform 300ms linear,opacity 300ms linear,-webkit-transform 300ms linear}nav .burger-3{margin-bottom:0}nav li{width:80%;list-style:none;font-family:"Quicksand",sans-serif;font-weight:700;font-size:2rem;transition:all 200ms ease-in-out}nav li.selected{color:#f5ed00}nav li:hover{cursor:pointer;font-size:2.5rem}nav ul{overflow:hidden;height:80%;width:100%;visibility:hidden;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center}.show-nav .burger-1{position:absolute;left:0;top:50%;-webkit-transform:rotate(45deg);transform:rotate(45deg)}.show-nav .burger-2{opacity:0;visibility:hidden}.show-nav .burger-3{position:absolute;right:0;top:50%;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.show-nav ul{visibility:visible}canvas{z-index:0}
.home{background-color:#e6e6e6;height:100vh;width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.home h1{font-size:3rem;color:#0d2c54;width:80%;margin-bottom:20px;text-shadow:1px 1px rgba(0,0,0,.2)}.home h2{font-size:2rem;color:#0d2c54;width:80%;text-shadow:1px 1px rgba(0,0,0,.2)}@media(min-width: 900px){.home{-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.home h1{color:#0d2c54;font-size:5rem}.home h2{color:#00b8cd;font-size:3rem}.home h2 span{color:#0d2c54;font-style:italic}}
.about{height:100vh;width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;background-color:#e6e6e6}.about h1{font-size:3rem;color:#0d2c54;width:80%;margin-top:10%;margin-bottom:10%}.about h2{font-size:2rem;color:#0d2c54;width:80%}.about .down-arrow{font-size:4rem;position:absolute;bottom:16vh}@media(min-width: 900px){.about{-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.about h1{color:#0d2c54;font-size:5rem}.about h2{color:#00b8cd;font-size:3rem}.about h2 span{color:#0d2c54;font-style:italic}}
.contact{height:100vh;width:100%;background-color:none;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;color:#0d2c54;background-color:#e6e6e6}.contact h1{font-size:3rem}
.scene{position:fixed;width:100%;transition:all 1s ease-in-out}.scene .info-btn{position:absolute;z-index:1;font-size:3rem;top:5vh;left:5vw;color:rgba(0,0,0,.3)}.scene .info-btn:hover{cursor:pointer}
.projects-menu{position:fixed;height:100vh;width:100vw;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;z-index:1}.projects-menu .projects-menu-wrapper{position:relative;height:70vh;width:70vw;background-color:#add8e6;border-radius:5px}.projects-menu .projects-menu-wrapper .close{position:absolute;top:0;right:0}.projects-menu .projects-menu-wrapper .close:hover{cursor:pointer}
.not-found{height:100vh;width:100vw;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}
footer{position:fixed;height:10vh;width:100%;bottom:0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.footer-wrapper{background-color:#8fde20;height:100%;width:100%;display:grid;place-items:center;border-radius:3vw 3vw 0 0;border-top:1vh solid #73c401}.footer-wrapper{position:absolute}.footer-wrapper:hover{cursor:pointer;background-color:#9bd844}@media(min-width: 900px){.footer-wrapper{width:80%}}
